body {
  background-color: brown;
  color: white;
  text-align: center;
  font-size: 25px;
}
#text-area {
  width: 80%;
  height: 200px;
}
#speed-input {
  width: 208px;
}
#speed-input.in {
  color: rgba(255, 255, 255, 0.7)
}
#text-area, #speed-input {
  border-radius: 5px;
  border: 2px solid white;
  background-color: transparent;
  color: white;
  padding: 5px;
  font-size: 15px;
  resize: none;
}
#text-area::-webkit-input-placeholder, #speed-input::-webkit-input-placeholder {
  color: white;
}
#text-area:-ms-input-placeholder, #speed-input:-ms-input-placeholder {
  color: white;
}
#text-area::placeholder, #speed-input::placeholder {
  color: white;
}
#speed-input:active, #speed-input:hover, #speed-input:focus {
  outline: none;
  color: white;
}
#text-area:active, #text-area:hover, #text-area:focus {
  outline: none;
}
button {
  height: 40px;
  width: 100px;
  background-color: white;
  border-radius: 2px;
  border: 2px solid white;
  margin: 10px;
  color: brown;
  padding: 2px;
  font-size: 20px;
}
button:focus {
  outline: none;
}
button:active {
  background-color: transparent;
  color: white;
}
button-div {
  text-align: center;
}
#num-player {
  font-size: 3em;
}

textarea.shake {
    -webkit-animation: shake 0.1s ease-in-out 0.1s infinite alternate;
}

@-webkit-keyframes shake {
 from {
    -webkit-transform: rotate(10deg);
 }
 to {
   -webkit-transform-origin:center center;
   -webkit-transform: rotate(-10deg);
 }
}
#speed-output {
  display: inline;
  font-size: 20px
}
#speed-container {
  padding: 10px;
}



